import { createRoot } from "react-dom/client";
import {
  createBrowserRouter,
  RouterProvider,
} from "react-router-dom";
import App from "./App";
import Allproducts from "./Products/Allproducts";
import Allphone from "./Phone/Allphone";
import Allabout from "./Aboutus/Allabout";
import Allupdates from "./Updates/Allupdates";
import ScrollToTop from "react-scroll-to-top";
import Allshopping from "./Shopping/Allshopping";
import Allcart from "./Cart/Allcart";
const router = createBrowserRouter([
  {
    path: "/",
    element: <App/>
  },
  {
    path: "Products",
    element: <Allproducts/>,
  },
  {
    path: "Home",
    element: <Allphone/>,
  },
  {
    path: "AboutUs",
    element: <Allabout/>,
  },
  {
    path: "Updates",
    element: <Allupdates/>,
  },
  {
    path: "Shopping",
    element: <Allshopping/>,
  },
  {
    path: "Cart",
    element: <Allcart/>,
  },
]);

createRoot(document.getElementById("root")).render(
  <div>

    <RouterProvider router={router} />
    <ScrollToTop smooth
    viewBox="0 0 448 512"
    svgPath="M246.6 9.4c-12.5-12.5-32.8-12.5-45.3 0l-128 128c-12.5 12.5-12.5 32.8 0 45.3s32.8 12.5 45.3 0L192 109.3 192 320c0 17.7 14.3 32 32 32s32-14.3 32-32l0-210.7 73.4 73.4c12.5 12.5 32.8 12.5 45.3 0s12.5-32.8 0-45.3l-128-128zM64 352c0-17.7-14.3-32-32-32s-32 14.3-32 32l0 64c0 53 43 96 96 96l256 0c53 0 96-43 96-96l0-64c0-17.7-14.3-32-32-32s-32 14.3-32 32l0 64c0 17.7-14.3 32-32 32L96 448c-17.7 0-32-14.3-32-32l0-64z" 
    color= "#fff"
    style={{backgroundColor : "#000"}} />
  </div>
);